* {
    box-sizing: border-box;
}
figure {
    margin: 0;
    cursor: pointer;
}
img {
    height: 234px;
    max-width: 100%;
    vertical-align: middle;
}
.movieName {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-weight: 600;
    color: white;
    height: 234px;
    width: 10.3%;
    cursor: pointer;
    opacity: 0;
    transition: .2s;
    margin-top: -294px;
}
.movieName:hover {
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.3);
}
.movie figcaption {
    padding: 1em;
}
.movieTitle {
    font-size: 10px;
    font-weight: normal;
    font-feature-settings: "smcp";
    font-variant: small-caps;
    margin: 0;
    color: white;
    overflow: hidden;
}
.main {
    width: 12.5%;
}
.movie {
    position: relative;
    /* width: 11.4%; */
    box-shadow: 0 0 1em rgb(0 0 0 / 30%);
    margin: 0px 15px 15px 0px;
}